.JumboGrove {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.JumboGrove .JGUI > nav {
  padding: 0 1rem;
}
.JumboGrove .JGUI > nav {
  overflow: auto;
}
.JumboGrove .JGUI > aside {
  padding: 0 1rem;
  overflow: auto;
  background-color: white;
}
.JumboGrove .JGUI > aside ul {
    list-style-type: none;
    padding-left: 0;
}
.JumboGrove .JGUI > aside ul ul ul {
      list-style-type: disc;
      list-style-position: inside;
}
.JumboGrove .JGUI > article {
  max-width: 34rem;
}
.JumboGrove .JGUI > nav {
  display: none;
}
.JumboGrove .JGUI nav.m-within-aside {
  display: block;
}
@media (max-width: 51rem) {
.JumboGrove .JGUI > aside {
    position: fixed;
    top: 2px;
    right: 2px;
    bottom: 2px;
    left: 2px;
    overflow: scroll;
}
.JumboGrove .JGUI > aside.m-closed {
      background-color: transparent;
      pointer-events: none;
}
.JumboGrove .JGUI > aside.m-closed .JGAsideContents {
        display: none;
}
.JumboGrove .JGUI > aside.m-open {
      border: 1px solid black;
}
.JumboGrove .JGUI > aside.m-open .JGAsideToggle {
        top: 2px;
        right: 2px;
        width: 44px;
        height: 44px;
        line-height: 44px;
}
.JumboGrove .JGUI > aside .JGAsideToggle {
      position: absolute;
      top: 3px;
      right: 3px;
      width: 44px;
      height: 44px;
      line-height: 44px;
      text-align: center;
      font-size: 36px;
      background-color: black;
      color: white;
      cursor: pointer;
      pointer-events: initial;
}
}
@media (min-width: 51rem) and (max-width: 64rem) {
.JumboGrove .JGUI > article {
    max-width: 100%;
    width: 66%;
}
.JumboGrove .JGUI > aside {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 33%;
}
.JumboGrove .JGUI > aside .JGAsideToggle {
      display: none;
}
.JumboGrove .JGUI > aside.m-closed .JGAsideContents {
      display: block;
}
}
@media (min-width: 64rem) {
.JumboGrove .JGUI > article {
    max-width: 34rem;
    margin: auto;
}
.JumboGrove .JGUI > nav {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    right: calc(50% + 20rem);
}
.JumboGrove .JGUI nav.m-within-aside {
    display: none;
}
.JumboGrove .JGUI > aside {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: calc(50% + 20rem);
}
.JumboGrove .JGUI > aside .JGAsideToggle {
      display: none;
}
.JumboGrove .JGUI > aside.m-closed .JGAsideContents {
      display: block;
}
}
.JGUIScrollSpacer {
  height: 2000px;
}
.JumboGrove aside ul li {
  list-style-type: none;
}
