img.headshot {
  image-rendering: optimizeSpeed;
  /* STOP SMOOTHING, GIVE ME SPEED  */
  image-rendering: -moz-crisp-edges;
  /* Firefox                        */
  image-rendering: -o-crisp-edges;
  /* Opera                          */
  image-rendering: -webkit-optimize-contrast;
  /* Chrome (and eventually Safari) */
  image-rendering: -o-pixelated;
  image-rendering: pixelated;
  /* Chrome */
  image-rendering: optimize-contrast;
  /* CSS3 Proposed                  */
  -ms-interpolation-mode: nearest-neighbor;
  /* IE8+                           */
  display: block;
  margin: auto;
  width: 64px;
  height: 64px;
}

.JGHTMLAddition, .JGHTMLItem {
  opacity: 0;
}
.m-inactive .JGHTMLAddition, .m-inactive .JGHTMLItem {
  opacity: 1;
}
.JGHTMLItem.m-inactive {
  opacity: 1;
}
.JGHTMLAddition.m-animated, .JGHTMLItem.m-animated {
  -webkit-animation: JGAdditionOpacity 1s;
          animation: JGAdditionOpacity 1s;
  opacity: 1;
}
@-webkit-keyframes JGAdditionOpacity {
0% { opacity: 0;
}
100% { opacity: 1;
}
}
@keyframes JGAdditionOpacity {
0% { opacity: 0;
}
100% { opacity: 1;
}
}

